.Main {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  .gradient-btn {
    background: linear-gradient(to right, #DC8064, #E98C70, #FB9E82, #FDBB9F, #FFCAAE, #F8AD92, #F19176);
    color: white;
   
    font-size: 0.875rem; 
    /* transition: background 0.2s ease; */
    border: none;
    cursor: pointer;
  }
  
  .gradient-btn:hover {
    filter: brightness(1.1);
  }